"use client";
import { useEffect, useState } from "react";

export default function DataTable({ columns, data, onEdit, onDelete }) {
  const [sortConfig, setSortConfig] = useState({ key: "codigo", direction: "asc" });
  const [searchField, setSearchField] = useState(columns[0]?.key || "");
  const [searchValue, setSearchValue] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [isVertical, setIsVertical] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsVertical(window.innerWidth < window.innerHeight);
    };
    window.addEventListener("resize", handleResize);
    handleResize();
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const formatCpf = (cpf) => {
    if (!cpf) return "";
    const cleaned = cpf.replace(/\D/g, "");
    return cleaned.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/, "$1.$2.$3-$4");
  };

  const sortedData = [...data].sort((a, b) => {
    if (!sortConfig.key) return 0;
    const valueA = a[sortConfig.key];
    const valueB = b[sortConfig.key];
    return typeof valueA === "number" && typeof valueB === "number"
      ? sortConfig.direction === "asc" ? valueA - valueB : valueB - valueA
      : sortConfig.direction === "asc"
      ? String(valueA).localeCompare(String(valueB))
      : String(valueB).localeCompare(String(valueA));
  });

  const filteredData = sortedData.filter((item) => {
    const value = item[searchField]?.toString().toLowerCase() || "";
    return value.includes(searchValue.toLowerCase());
  });

  const totalPages = Math.ceil(filteredData.length / itemsPerPage);
  useEffect(() => {
    if (currentPage > totalPages) {
      setCurrentPage(totalPages > 0 ? totalPages : 1);
    }
  }, [currentPage, totalPages]);

  const startIndex = (currentPage - 1) * itemsPerPage;
  const paginatedData = filteredData.slice(startIndex, startIndex + itemsPerPage);

  const handleSort = (key) => {
    setSortConfig((prev) => ({
      key,
      direction: prev.key === key && prev.direction === "asc" ? "desc" : "asc",
    }));
  };

  return (
    <div className="bg-white shadow-md rounded-lg overflow-hidden mt-6 p-4">
      <div className="flex flex-wrap gap-4 mb-4">
        <select value={searchField} onChange={(e) => setSearchField(e.target.value)} className="border p-2 rounded-md">
          {columns.map((col) => (
            <option key={col.key} value={col.key}>{col.label}</option>
          ))}
        </select>
        <input type="text" value={searchValue} onChange={(e) => setSearchValue(e.target.value)} placeholder="Digite para buscar..." className="border p-2 rounded-md w-full" />
        <select value={itemsPerPage} onChange={(e) => setItemsPerPage(Number(e.target.value))} className="border p-2 rounded-md">
          <option value={10}>10 registros</option>
          <option value={20}>20 registros</option>
          <option value={50}>50 registros</option>
        </select>
      </div>

      {paginatedData.length === 0 ? (
        <p className="text-gray-600 text-center p-4">Nenhum registro encontrado.</p>
      ) : isVertical ? (
        <div className="flex flex-col gap-4">
          {paginatedData.map((item) => (
            <div key={item.codigo} className="border p-4 rounded-md shadow-sm">
              {columns.map((col) => (
                <div key={col.key} className="flex justify-between border-b pb-1 mb-1">
                  <strong>{col.label}:</strong>
                  <span>{col.key === "documento" ? formatCpf(item[col.key]) : col.render ? col.render(item) : item[col.key]}</span>
                </div>
              ))}
              <div className="flex justify-around mt-2">
                <button onClick={() => onEdit(item)} className="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600 transition-all">‚úèÔ∏è Editar</button>
                <button onClick={() => onDelete(item)} className="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-600 transition-all">üóëÔ∏è Excluir</button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <table className="w-full border-collapse">
          <thead className="sticky top-0 bg-gray-800 text-white">
            <tr>
              {columns.map((col) => (
                <th key={col.key} onClick={() => handleSort(col.key)} className="p-4 text-left cursor-pointer hover:bg-gray-700 transition">
                  {col.label} {sortConfig.key === col.key && (sortConfig.direction === "asc" ? "üîº" : "üîΩ")}
                </th>
              ))}
              <th className="p-4 text-center">Editar</th>
              <th className="p-4 text-center">Excluir</th>
            </tr>
          </thead>
          <tbody>
            {paginatedData.map((item, index) => (
              <tr key={item.codigo} className={`border-t ${index % 2 === 0 ? "bg-gray-100" : "bg-white"}`}>
                {columns.map((col) => (
                  <td key={col.key} className="p-4">
                    {col.key === "documento" ? formatCpf(item[col.key]) : col.render ? col.render(item) : item[col.key]}
                  </td>
                ))}
                <td className="p-4 text-center">
                  <button onClick={() => onEdit(item)} className="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600 transition-all">‚úèÔ∏è Editar</button>
                </td>
                <td className="p-4 text-center">
                  <button onClick={() => onDelete(item)} className="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-600 transition-all">üóëÔ∏è Excluir</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
